import { MoveDirection } from "types/move_direction";
import { IShape } from "components/canvas/shape";
import { CanvasShapeActions, Action } from "actions/canvas_shape_actions";

export interface State {
  shapes: IShape[];
  nextShapeId: number;
  currentMaxZIndex: number;
  selectedShape?: number;
}

export const initialState: State = {
  shapes: [],
  nextShapeId: 1,
  currentMaxZIndex: 1,
};
const controlPanelStepSizePx = 10;

function calculateNextCoordinate(topCoordinate: IPoint, moveDirection: MoveDirection): IPoint {
  switch (moveDirection) {
    case MoveDirection.UP:
      return { x: topCoordinate.x, y: topCoordinate.y - controlPanelStepSizePx };
    case MoveDirection.DOWN:
      return { x: topCoordinate.x, y: topCoordinate.y + controlPanelStepSizePx };
    case MoveDirection.LEFT:
      return { x: topCoordinate.x - controlPanelStepSizePx, y: topCoordinate.y };
    case MoveDirection.RIGHT:
      return { x: topCoordinate.x + controlPanelStepSizePx, y: topCoordinate.y };
  }
}

export function reducer(state: State = initialState, action: Action): State {
  switch (action.type) {
    case CanvasShapeActions.ADD_SHAPE: {
      const newShape: IShape = {
        type: action.payload.type,
        size: action.payload.size,
        topCoordinate: action.payload.drawCoordinate,
        id: state.nextShapeId,
        zIndex: state.currentMaxZIndex,
      };
      state.nextShapeId = state.nextShapeId + 1;
      return { ...state, shapes: [...state.shapes, newShape] };
    }

    case CanvasShapeActions.MOVE_SHAPE: {
      const { id, moveToCoord } = action.payload;

      return {
        ...state,
        shapes: state.shapes.map(shape => (shape.id === id ? { ...shape, topCoordinate: moveToCoord } : shape)),
      };
    }

    case CanvasShapeActions.SELECT_SHAPE: {
      return { ...state, selectedShape: action.payload.selectedCardId };
    }
    case CanvasShapeActions.STEP_SHAPE: {
      const moveDirection = action.payload.stepDirection;
      return {
        ...state,
        shapes: state.shapes.map(
          shape =>
            shape.id === state.selectedShape
              ? { ...shape, topCoordinate: calculateNextCoordinate(shape.topCoordinate, moveDirection) }
              : shape
        ),
      };
    }

    case CanvasShapeActions.BRING_TO_FRONT: {
      return {
        ...state,
        shapes: state.shapes.map(
          shape =>
            shape.id === state.selectedShape
              ? { ...shape, zIndex: Math.max(...state.shapes.map(s => s.zIndex)) + 1 }
              : shape
        ),
      };
    }
    case CanvasShapeActions.SEND_BACKWARD: {
      return {
        ...state,
        shapes: state.shapes.map(
          shape => (shape.id === state.selectedShape ? { ...shape, zIndex: 1 } : { ...shape, zIndex: shape.zIndex + 1 })
        ),
      };
    }
    default:
      return state;
  }
}
