import { IShape } from "components/canvas/shape";
import { ShapeType } from "types/shape_type";
import { MoveDirection } from "types/move_direction";

export enum CanvasShapeActions {
  ADD_SHAPE = "ADD_SHAPE",
  MOVE_SHAPE = "MOVE_SHAPE",
  BRING_TO_FRONT = "BRING_TO_FRONT",
  SEND_BACKWARD = "SEND_BACKWARD",
  SELECT_SHAPE = "SELECT_SHAPE",
  STEP_SHAPE = "STEP_SHAPE",
}

export interface AddShapeAction {
  type: CanvasShapeActions.ADD_SHAPE;
  payload: { type: ShapeType; size: number; drawCoordinate: IPoint };
}
export interface MoveShapeAction {
  type: CanvasShapeActions.MOVE_SHAPE;
  payload: { id: number; moveToCoord: IPoint };
}
export interface BringToFrontAction {
  type: CanvasShapeActions.BRING_TO_FRONT;
}
export interface SendBackwardAction {
  type: CanvasShapeActions.SEND_BACKWARD;
}
export interface SelectShapeAction {
  type: CanvasShapeActions.SELECT_SHAPE;
  payload: { selectedCardId?: number };
}

export interface StepShapeAction {
  type: CanvasShapeActions.STEP_SHAPE;
  payload: { stepDirection: MoveDirection };
}

export function addShape(type: ShapeType, size: number, drawCoordinate: IPoint): AddShapeAction {
  return { type: CanvasShapeActions.ADD_SHAPE, payload: { type, drawCoordinate, size } };
}
export function moveShape(cardId: number, moveToCoord: IPoint): MoveShapeAction {
  return { type: CanvasShapeActions.MOVE_SHAPE, payload: { id: cardId, moveToCoord } };
}
export function bringToFront(): BringToFrontAction {
  return { type: CanvasShapeActions.BRING_TO_FRONT };
}
export function bringBackwards(): SendBackwardAction {
  return { type: CanvasShapeActions.SEND_BACKWARD };
}
export function selectShape(selectedCardId?: number): SelectShapeAction {
  return { type: CanvasShapeActions.SELECT_SHAPE, payload: { selectedCardId } };
}
export function stepShape(stepDirection: MoveDirection): StepShapeAction {
  return { type: CanvasShapeActions.STEP_SHAPE, payload: { stepDirection } };
}

export type Action =
  | AddShapeAction
  | MoveShapeAction
  | BringToFrontAction
  | SendBackwardAction
  | SelectShapeAction
  | StepShapeAction;
