import * as React from "react";
import { State } from "reducers";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import { stepShape, bringToFront, bringBackwards } from "actions/canvas_shape_actions";
import { MoveDirection } from "types/move_direction";

interface IControlPanelProps {
  onShapeStepped?: (direction: MoveDirection) => void;
  onBringToFront?: () => void;
  onSendToBack?: () => void;
}

class ControlPanelComponent extends React.Component<IControlPanelProps> {
  public render(): JSX.Element {
    return (
      <div className="control-panel">
        <img
          onClick={this.bringToFront}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/mono-bring-stencil-to-front/mono-bring-stencil-to-front.png"
        />
        <img
          onClick={this.moveStepUp}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/aiga-up-arrow-bg/aiga-up-arrow-bg.png"
        />
        <img
          onClick={this.sendToBack}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/mono-send-stencil-to-back/mono-send-stencil-to-back.png"
        />
        <img
          onClick={this.moveStepLeft}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/aiga-left-arrow-bg/aiga-left-arrow-bg.png"
        />
        <img
          onClick={this.moveStepDown}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/aiga-down-arrow-bg/aiga-down-arrow-bg.png"
        />
        <img
          onClick={this.moveStepRight}
          style={{ width: "33%" }}
          src="https://www.iconspng.com/uploads/aiga-right-arrow-bg/aiga-right-arrow-bg.png"
        />
      </div>
    );
  }
  private moveStepUp = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onShapeStepped && this.props.onShapeStepped(MoveDirection.UP);
  };
  private moveStepLeft = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onShapeStepped && this.props.onShapeStepped(MoveDirection.LEFT);
  };
  private moveStepDown = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onShapeStepped && this.props.onShapeStepped(MoveDirection.DOWN);
  };
  private moveStepRight = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onShapeStepped && this.props.onShapeStepped(MoveDirection.RIGHT);
  };
  private bringToFront = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onBringToFront && this.props.onBringToFront();
  };
  private sendToBack = (e: React.MouseEvent<HTMLElement>): void => {
    this.props.onSendToBack && this.props.onSendToBack();
  };
}

const mapDispatchToProps = {
  onShapeStepped: stepShape,
  onSendToBack: bringBackwards,
  onBringToFront: bringToFront,
};

export const ControlPanel = connect(
  undefined,
  mapDispatchToProps
)(ControlPanelComponent);
