import * as React from "react";
import { DropTarget, DropTargetMonitor } from "react-dnd";
import ReactDOM = require("react-dom");
import { connect } from "react-redux";
import { Dispatch } from "redux";
import "components/canvas/canvas.css";
import { DraggableTypes } from "types/draggable_types";
import { CanvasShape, ICanvasShapeProps } from "components/canvas/canvas_shape";
import { ShapeType } from "types/shape_type";
import { MoveDirection } from "types/move_direction";
import { IShape } from "components/canvas/shape";
import { State } from "reducers";
import { addShape, moveShape, selectShape, stepShape } from "actions/canvas_shape_actions";

export const canvasShapeDefaultSize = 100;

interface ICanvasProps {
  shapes: IShape[];
  selectedShape?: number;
  connectDropTarget?: (content: JSX.Element) => JSX.Element;
  onShapeAdded?: (type: ShapeType, size: number, drawCoordinate: IPoint) => void;
  onShapeMoved?: (id: number, drawCoordinate: IPoint) => void;
  onShapeSelected?: (selectedCardId?: number) => void;
  onShapeStepped?: (direction: MoveDirection) => void;
}

export class CanvasComponent extends React.Component<ICanvasProps> {
  private offsetX!: number;
  private offsetY!: number;

  constructor(props: ICanvasProps) {
    super(props);

    this.state = {};
    this.selectComponent = this.selectComponent.bind(this);
    this.unselectComponent = this.unselectComponent.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
  }

  public render(): JSX.Element {
    const { shapes, connectDropTarget } = this.props;

    if (connectDropTarget) {
      return connectDropTarget(this.renderContent(shapes));
    } else {
      return this.renderContent(shapes);
    }
  }

  public componentDidMount(): void {
    const drawingCanvas: Element = ReactDOM.findDOMNode(this) as Element;
    const clientRect: ClientRect = drawingCanvas.getBoundingClientRect();

    this.offsetX = clientRect.left;
    this.offsetY = clientRect.top;
  }

  public componentWillMount(): void {
    document.addEventListener("keydown", this.handleKeyDown.bind(this));
  }
  public componentWillUnmount(): void {
    document.removeEventListener("keydown", this.handleKeyDown.bind(this));
  }

  public draw(type: ShapeType, drawCoordinate: IPoint): void {
    this.props.onShapeAdded && this.props.onShapeAdded(type, canvasShapeDefaultSize, drawCoordinate);
  }

  public moveShape(id: number, drawCoordinate: IPoint): void {
    this.props.onShapeMoved && this.props.onShapeMoved(id, drawCoordinate);
  }

  public convertDragCoordToDrawCoord(dragCoordinate: IPoint): IPoint {
    return { x: dragCoordinate.x - this.offsetX, y: dragCoordinate.y - this.offsetY };
  }

  public coordinateIsInBoundsOf(coordinate: IPoint, size: number): boolean {
    const drawingCanvas: Element = ReactDOM.findDOMNode(this) as Element;

    return (
      coordinate.x >= 0 &&
      coordinate.y >= 0 &&
      coordinate.x + size <= drawingCanvas.clientWidth &&
      coordinate.y + size <= drawingCanvas.clientHeight
    );
  }

  private handleKeyDown = (e: React.KeyboardEvent): void => {
    e.stopPropagation();

    const keyCode = e.keyCode;
    if (keyCode >= 37 && keyCode <= 40) {
      this.props.onShapeStepped && this.props.onShapeStepped(this.getMoveDirection(e.keyCode));
    }
  };

  private getMoveDirection(keyCode: number): MoveDirection {
    switch (keyCode) {
      case 37:
        return MoveDirection.LEFT;
      case 38:
        return MoveDirection.UP;
      case 39:
        return MoveDirection.RIGHT;
      case 40:
        return MoveDirection.DOWN;
    }
    throw new Error("Not expected move direction");
  }

  private renderContent(shapes: IShape[]): JSX.Element {
    return (
      <div className="canvas" onClick={this.unselectComponent}>
        {shapes.map(shape => (
          <CanvasShape
            shape={shape}
            key={shape.id}
            onShapeSelection={this.selectComponent}
            isSelected={this.props.selectedShape === shape.id}
          />
        ))}
      </div>
    );
  }

  private selectComponent(key: number): void {
    const shape = this.props.shapes.find(s => s.id === key);
    if (shape === undefined) {
      throw new Error("Unknown item has been dragged");
    }
    this.props.onShapeSelected && this.props.onShapeSelected(key);
  }

  private unselectComponent(): void {
    this.props.onShapeSelected && this.props.onShapeSelected(undefined);
  }
}

const canvasTarget = {
  drop: (props: ICanvasProps, monitor: DropTargetMonitor, component: CanvasComponent) => {
    const dragCoordinate = monitor.getSourceClientOffset() as {
      x: number;
      y: number;
    };
    const drawCoordinate = component.convertDragCoordToDrawCoord(dragCoordinate);
    const size = monitor.getItem().size || canvasShapeDefaultSize;

    if (component.coordinateIsInBoundsOf(drawCoordinate, size)) {
      if (monitor.getItemType() === DraggableTypes.PALETTE_SHAPE) {
        component.draw(monitor.getItem().type, drawCoordinate);
      }
      if (monitor.getItemType() === DraggableTypes.CANVAS_SHAPE) {
        component.moveShape(monitor.getItem().id, drawCoordinate);
      }
    }
  },
};

const CanvasDrop = DropTarget(
  [DraggableTypes.PALETTE_SHAPE, DraggableTypes.CANVAS_SHAPE],
  canvasTarget,
  (connector: any, monitor: DropTargetMonitor) => {
    return { connectDropTarget: connector.dropTarget(), canDrop: monitor.canDrop() };
  }
)(CanvasComponent);

function mapStateToProps(state: State): ICanvasProps {
  return {
    shapes: state.canvasState.shapes,
    selectedShape: state.canvasState.selectedShape,
  };
}

const mapDispatchToProps = {
  onShapeAdded: addShape,
  onShapeMoved: moveShape,
  onShapeSelected: selectShape,
  onShapeStepped: stepShape
};

export const Canvas = connect(
  mapStateToProps,
  mapDispatchToProps
)(CanvasDrop);
